.company-wrapper{
    .swiper{
        margin: 0px;
    }

    .title-swiper{
        @include flex;
        margin: 70px 0px 0px 0px;

        @include pad{
            @include flex($d:column-reverse);
        }
        .company-title{
            @include flex($d:column);
            flex: 3;
            padding: 68px 60px;
            gap: 12px;
            background: $c-secondary-white;
            @include pad{
                padding: 52px 40px;

            }
            @include mini-pad{
                padding: 44px 36px;

            }
            @include mobile{
                padding: 28px 16px;
            }
            .company-category{
                @include flex($a-i:center);
                gap: 8px;
                img{
                    @extend %bb;
                    padding: 4px;
                    width: 100%;
                    height: fit-content;
                    object-fit: cover;
                    max-width: 60px;
                    filter: drop-shadow(0px 3px 6px rgba(0, 0, 0, 0.161));
                    border-radius: 4px;
                    @include mini-pad{
                        max-width: 50px;
                    }
                }
                .category-content{
                    >p{
                        font-weight: 700;
                    }

                    .address{
                        @include flex($a-i:center);
                        gap:4px;
                        color: $c-gray;
                        @include mini-pad{
                            @include flex($a-i:flex-start);
                        }
                    }
                }

            }
            hr{
                border-color: $c-secondary;
                width: 100%;
            }
            .company-img{
                @include flex($j-c:space-between,$a-i:center);

                @include mobile{
                    @include flex($d:column,$a-i:center);
                    gap: 20px;
                }
                
                img{
                    @include mentor-img(120px);
                }
                .img-award{
                    @include flex;
                    gap: 12px;
                    img{
                        @include mentor-img(65px);
                        height: fit-content;
                    }
                }
            }
        }

        .company-swiper{
            width: 200px;
            flex: 5;

            @include pad{
                width: 100%;
            }

            .swiper{
                @include pad{
                    width: 100%;
                    min-height: unset;
                    max-height: unset;
                }
                .swiper-slide >img{
                    padding: 0px;
                }
            }

        }
    }

    .tabWrapper{
        @extend %page-content;
        width: auto;
        padding-top: 20px;

        @include pad{
            padding-top: 30px;
        }
        @include mini-pad{
            overflow-x: auto;
            padding: 20px 30px 0px 30px;
        }

        .linkTab{
            gap: 0px;
            border-bottom: 1px solid #DDD;
            white-space: nowrap;
            width: fit-content;
            margin: auto;

            >a{
                width: 200px;
                text-align: center;
                box-sizing: border-box;
                @include pad{
                    width: 150px;
                }
                @include mini-pad{
                    width: 120px;
                }
            }
        }
    }
    
    .companyStory{
        @extend %page-content;
        @include flex($d:column,$a-i:flex-start);
        margin: auto;
        gap:48px;
        padding: 88px 120px;
        background: $c-white;
        @include pad{
            padding: 66px 80px;
            gap:32px;
        }
        @include mini-pad{
            padding: 40px 30px;
            gap:24px;
        }
        >.title{
            border-bottom: none;
            padding: 0px;
        }

        .walfareSection{
            @include flex($d:column,$a-i:flex-start);
            gap:52px;
            position: relative;

            .walfare-group{
                @include flex;
                width: 100%;

                @include mini-pad{
                    @include flex($d:column);
                    gap: 12px;
                }



                .group-title{
                    flex:0.3;
                    color: $c-primary;
                }

                .highlight{
                    .walfare-item{
                        li:nth-child(1){
                            color: $c-secondary;
                        }
                    }
                }

                .item-group{
                    flex: 1;
                    @include basic_grid(4,1fr,40px,32px);

                    @include mini-pad{
                        @include basic_grid(2,1fr,40px,32px);
                    }

                    .walfare-item{
                        @include flex($d:column);
                        gap: 8px;

                        @include mini-pad{
                            gap:2px;
                        }

                        li{
                            color: $c-gray;
                        }

                    }
                }
            }
        }
        .link {
            @include flex($d:column,$a-i: flex-start, $j-c: space-between, $w: wrap);
            gap: 15px;

            .category {
                ul {
                    @include flex($w: wrap);
                    gap: 10px;

                    li {
                        @extend %pointer;

                        a {
                            @extend %dib;
                            color: $c-primary-deep;
                            padding: 0.5rem 1rem;
                            transition: all ease .4s;
                            border: 1px solid #d9d9d9;
                            border-radius: 5px;

                            &:hover {
                                color: $c-white;
                                background: $c-primary-deep;
                                border-color: $c-primary-deep;
                            }
                        }
                    }
                }
            }

            .media-share {
                @include flex($a-i: center, $w: wrap);
                gap: 10px;

                .media-item {
                    a {
                        @extend %dib;
                        @extend %bb;
                        width: 40px;
                        height: 40px;
                        padding: 0.5rem;
                        border: 1px solid #DDDDDD;
                        border-radius: 100px;
                        transition: all ease .4s;

                        &:hover {
                            border-color: $c-primary-deep;

                            svg {
                                path {
                                    fill: $c-primary-deep;
                                }
                            }
                        }

                        svg {
                            width: 100%;
                            height: 100%;

                            path {
                                transition: all ease .4s;
                            }
                        }
                    }
                }
            }
        }
    }


}