//===============
// (flexgridmixin)
%ma{
    margin: auto;
}

%page-content{
    margin: auto;
    padding: 0 30px;
    max-width: 1920px; //
    box-sizing: border-box;
}

%db{
    display: block;
}

%dib{
    display: inline-block;
}

%dn{
    display: none;
}

%bb{
    box-sizing: border-box;
}

//(absolutemixin)
%rel{
    position: relative;
}

%sticky{
    position: sticky;
    top: 0;
    left: 0;
}

//font
%tac{
    text-align: center;
}

//
%pointer{
    cursor: pointer;
}

